import { useEffect, useState } from "react";
import { useCartContext } from "../../context/CartContext";
import { collection, addDoc, getFirestore } from "firebase/firestore";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";

const Checkout = () => {
  const { cartList, totalCart, emptyCart } = useCartContext();
  const [idOrder, setIdOrder] = useState("");
  const [comunas, setComunas] = useState([]);
  const [order, setOrder] = useState({
    name: "",
    lastname: "",
    rut: "",
    email: "",
    phone: "",
    comuna: "",
    address: "",
    date: new Date(),
    items: cartList.map((item) => ({
      id: item.id,
      title: item.name,
      quantity: item.quantity,
      price: item.price * item.quantity,
    })),
    quantity: cartList.reduce((acc, item) => acc + item.quantity, 0),
    total: totalCart(),
  });

  const generarOrden = (e) => {
    e.preventDefault();
    const db = getFirestore();
    const orderCollection = collection(db, "orders");
    addDoc(orderCollection, order)
      .then( resp => setIdOrder(resp.id))
      .catch(err => console.log(err))
      .finally(() => {
        // emptyCart();
        setOrder({
          name: "",
          lastname: "",
          rut: "",
          email: "",
          phone: "",
          comuna: "",
          address: "",
        });
        toast(`Orden ${idOrder} generada exitosamente`, {
          position: "top-center",
          autoClose: 100000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
          closeButton: false,
        });
      });
  };

  const handleChange = (e) => {
    setOrder({
      ...order,
      [e.target.name]: e.target.value,
    });
  };

  useEffect(() => {
    const getComunas = async () => {
      const response = await fetch(
        "https://apis.digital.gob.cl/dpa/regiones/13/comunas"
      );
      const data = await response.json();
      setComunas(data);
    };
    getComunas();
  }, []);

  return (
    <section className="checkoutSection">
      <h2>CheckOut</h2>
      {cartList.length < 1 ? 
(<Link to="/all" className="backStore">Volver a la tienda</Link>) 
: 
(
      <form
        className="d-flex flex-column align-items-center"
        onSubmit={generarOrden}
        onChange={handleChange}
      >
        <input
          type="text"
          name="name"
          id="name"
          placeholder="Nombre"
          defaultValue={order.name}
        />
        <input
          type="text"
          name="lastname"
          id="lastname"
          placeholder="Apellido"
          defaultValue={order.lastname}
        />
        <input
          type="text"
          name="rut"
          id="rut"
          placeholder="RUT"
          defaultValue={order.rut}
        />
        <input
          type="email"
          name="email"
          id="email"
          placeholder="Correo electrónico"
          defaultValue={order.email}
        />
        <input
          type="tel"
          name="phone"
          id="phone"
          placeholder="Teléfono"
          defaultValue={order.phone}
        />
        <select name="comuna" id="comunas">
          {comunas.map((item) => (
            <option key={item.codigo} value={item.id}>
              {item.nombre}
            </option>
          ))}
        </select>
        <input
          type="text"
          name="address"
          id="address"
          placeholder="Dirección"
        />
        <button type="submit" className="btn-order">
          Generar Orden
        </button>
      </form>
      )}
    </section>
  );
};

export default Checkout;
